
.box{
    width: 100%;
    height: 100vh;
    display:flex;
    justify-content:center;
    align-items:center;
}

.form{
    width: 60%;
    padding: 30px;
}
.row{
    margin: 5px;
}
input,textarea{
    background-color: #16161d!important;
    color: white!important;
}
input ::placeholder{
    color: white!important;
}
textarea ::placeholder{
    color: white!important;
}


/* modal css  */


.modal{
    padding: 10px;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
    width: 400px;
    height: 240px;
    background-color: #fff;
    color: black;
    position: absolute;
    top:0;
    left: auto;
    transform: translateY(-50%) scale(0.01);
    visibility: hidden;
    transition: all 0.4s;
    box-sizing: border-box;

}
.open-modal{
    transform: translateY(100%) scale(1);
    visibility: visible;
}

.modal h2{
    margin: 10px;
}
.mt{
    font-size: 20px;
}




@media screen and (max-width:900px) {
    .form{
        width: 95%;
        padding: 0;
    }
}