.topBox{
    width: 100%;
    height:100%;
    display:flex;
    justify-content:center!important;
    color: white;
}
.cateBox{ 
    
    width: auto;
    min-height: 400px;
    margin:auto;
    background-color: rgb(213, 67, 67);
    display:flex;
    flex-wrap: wrap;
    justify-content:space-between;
    align-items: center;
    
}   
.smBox{
    width: auto;
    font-weight: bolder;
    font-size: 28px;
    padding: 30px;
    height: 100px;
    margin: 30px;
    display:flex;
    justify-content:center;
    align-items:center;
    background-color: blue;
}
a{
    text-decoration: none!important;
    color: inherit;
}
a:hover{
    color: inherit;
}

@media screen and (max-width:421px) {
    .cateBox{
        justify-content:center;

    }
}