.container2{
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.box2{
    background-color: #a7acb8;
    align-self: center;
    width: 50%;
    min-height: 35%;
    padding: 50px;
    border-radius: 21px;
    /* box-shadow: 8px 4px 22px 0px rgba(186,178,178,0.75); */

}
.box2 input::placeholder{
    color: #5b342e!important;
    font-weight: bolder!important;
}

@media screen and (max-width:910px) {
    .box2{
        width: 90%;
        padding: 10px;
        border-radius: 12px;

    }
}